<template>
  <div id="colorpicker">
    <h3>基础用法</h3>
    <el-row type="flex" class="row-bg color-box" justify="space-around">
      <el-col :span="12">
        <div class="block">
          <span class="demonstration">有默认值</span>
          <el-color-picker v-model="color1"></el-color-picker>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="block">
          <span class="demonstration">无默认值</span>
          <el-color-picker v-model="color2"></el-color-picker>
        </div>
      </el-col>
    </el-row>
    <h3>自定义颜色</h3>
    <el-row type="flex" class="row-bg color-box" justify="space-around">
      <el-col :span="24">
        <el-color-picker v-model="color5" show-alpha :predefine="predefineColors">
        </el-color-picker>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        color1: '#409EFF',
        color2: null,
        color5: 'rgba(255, 69, 0, 0.68)',
        predefineColors: [
          '#ff4500',
          '#ff8c00',
          '#ffd700',
          '#90ee90',
          '#00ced1',
          '#1e90ff',
          '#c71585',
          'rgba(255, 69, 0, 0.68)',
          'rgb(255, 120, 0)',
          'hsv(51, 100, 98)',
          'hsva(120, 40, 94, 0.5)',
          'hsl(181, 100%, 37%)',
          'hsla(209, 100%, 56%, 0.73)',
          '#c7158577'
        ]
      }
    }
  };

</script>

<style lang="scss" scoped>
  .color-box {
    padding: 4%;
    border: 1px solid #ccc;
    border-radius: 10px;
    box-shadow: 3px 3px 8px #ccc;
  }

</style>



